<template>
    <v-dialog v-model="active" max-width="350" persistent>
        <v-card>
            <v-card-title class="text-h6 text-center justify-center">
                <span> {{ $t($store.state['ui'].message.alert.title) }} </span>
            </v-card-title>

            <v-card-text class="text-center">
                <span> {{ $store.state['ui'].message.alert.text }} </span>
            </v-card-text>

            <v-card-actions class="pb-4">
                <v-spacer></v-spacer>
                <v-btn color="info" small @click="active = false"> Ok </v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    name: 'Alert',
    computed: {
        active: {
            get() {
                return this.$store.state['ui'].message.alert.visible;
            },
            set(active) {
                this.$store.commit('ui/ALERT', { title: 'message.alert.title', text: null, visible: active });
            }
        },
    },
}
</script>

