<template>
    <v-snackbar v-model="active" :color="$store.state['ui'].message.notify.color" elevation="3" :timeout="$store.state['ui'].message.notify.timeout" top centered>
        <span> {{ $store.state['ui'].message.notify.text }} </span>
        <template v-slot:action="{ attrs }">
            <v-btn @click="active = false" v-bind="attrs" icon small>
                <v-icon small>mdi-close</v-icon>
            </v-btn>
        </template>
    </v-snackbar>
</template>

<script>
export default {
    name: 'Alert',
    computed: {
        active: {
            get() {
                return this.$store.state['ui'].message.notify.visible;
            },
            set(active) {
                this.$store.commit('ui/NOTIFY', { title: 'message.notify.title', text: null, visible: active });
            }
        },
    },
}
</script>

